//WCManoNegraBta
@font-face {
    font-family: 'wc_mano_negra_btaregular';
    src: url('../../fonts/wcmanonegrabta-webfont.woff') format('woff'),
         url('../../fonts/wcmanonegrabta-webfont.svg#wc_mano_negra_btaregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

//Aleo light
@font-face {
    font-family: 'Aleo';
    src: url('../../fonts/aleo-light-webfont.woff') format('woff'),
         url('../../fonts/aleo-light-webfont.svg#aleolight') format('svg');
    font-weight: 300;
    font-style: normal;
}

//Aleo light italic
@font-face {
    font-family: 'Aleo';
    src: url('../../fonts/aleo-lightitalic-webfont.woff') format('woff'),
         url('../../fonts/aleo-lightitalic-webfont.svg#aleolightitalic') format('svg');
    font-weight: 300;
    font-style: italic;
}

//Aleo regular
@font-face {
    font-family: 'Aleo';
    src: url('../../fonts/aleo-regular-webfont.woff') format('woff'),
         url('../../fonts/aleo-regular-webfont.svg#aleoregular') format('svg');
    font-weight: 400;
    font-style: normal;
}

//Aleo bold
@font-face {
    font-family: 'Aleo';
    src: url('../../fonts/aleo-bold-webfont.woff') format('woff'),
         url('../../fonts/aleo-bold-webfont.svg#aleobold') format('svg');
    font-weight: 700;
    font-style: normal;
}

//Font-awesome 5
@font-face {
  font-family: 'Font Awesome 5 Free';
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/fa-regular-400.woff") format("woff"),
       url("../../fonts/fa-regular-400.svg#fontawesome") format("svg");
}

@font-face {
  font-family: 'Font Awesome 5 Brands';
  font-style: normal;
  font-weight: normal;
  src: url("../../fonts/fa-brands-400.woff") format("woff"),
       url("../../fonts/fa-brands-400.svg#fontawesome") format("svg");
}

@font-face {
  font-family: 'Font Awesome 5 Free';
  font-style: normal;
  font-weight: 900;
  src: url("../../fonts/fa-solid-900.woff") format("woff"),
       url("../../fonts/fa-solid-900.svg#fontawesome") format("svg");
}